.App {
}

#welcome {
	padding-top:2vh;
	padding-left: 5vw;
	padding-right: 5vw;
	font-weight: bold;
	font-size: 5vh;
}

.nav-tabs {
	background-color: #8A4F7D;
}

.nav-item {
	color: white;
}

.callToday {
	padding-left: 5vw;
	padding-right: 5vw;
	padding-top:2vh;
	font-size: 3vh;
	font-weight: bold;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
